html {
  font-size: 62.5%;
  box-sizing: border-box;
  font-family: "Space Mono", monospace;
}

*,
*::before,
*::after {
  box-sizing: inherit;
}

body {
  background-color: #c5e4e7;
  color: #7f9c9f;
}
body h1 {
  color: #00494d;
  text-transform: uppercase;
  letter-spacing: 0.6rem;
  text-align: center;
  margin: 5rem 0;
  line-height: 2.8rem;
}

.container {
  width: 95%;
  background-color: white;
  max-width: 80rem;
  border-radius: 2rem;
  margin: 0 auto;
  padding: 4rem;
}
@media (min-width: 992px) {
  .container {
    display: grid;
    grid-template-columns: repeat(2, 1fr);
    gap: 0;
  }
}

input {
  font-weight: 700;
  border-style: none;
  text-align: right;
  background-color: #f4fafa;
  font-size: 2rem;
  color: #00494d;
  outline: none;
}
input::-webkit-inner-spin-button, input::-webkit-outer-spin-button {
  -webkit-appearance: none;
  margin: 0;
}

.attribution {
  text-align: center;
  font-size: 1.6rem;
  margin-top: 2rem;
}
.attribution a {
  text-decoration: none;
  color: #26c0ab;
}

.input {
  background-color: #f4fafa;
  padding: 1rem;
  border-radius: 1rem;
  max-width: 30rem;
}
.input:hover {
  border: 0.1rem solid #26c0ab;
}

.btns {
  display: grid;
  grid-template-columns: repeat(3, 1fr);
  gap: 1rem;
  max-width: 30rem;
  margin: 0;
}
.btns .check {
  background-color: #26c0ab;
}
.btns #custom {
  z-index: 1;
  font-size: 2rem;
  max-width: 9rem;
  color: #5e7a7d;
  text-align: center;
}
.btns #custom:hover {
  color: #00494d;
  border: 0.1rem solid #26c0ab;
  border-radius: 1rem;
}

button {
  cursor: pointer;
  border-style: none;
  border-radius: 0.5rem;
  font-weight: 700;
  font-size: 1.5rem;
  padding: 1rem 1rem;
  margin: 0 auto;
  min-width: 9rem;
  background-color: #00494d;
  color: white;
}
button:hover {
  background-color: #A7F2E4;
  color: #000000;
}

.tip {
  margin-top: 2rem;
  background-color: #00494d;
  padding: 2rem;
  border-radius: 1rem;
  display: flex;
  flex-direction: column;
  justify-content: space-between;
}
.tip button {
  background-color: #0D686D;
  color: #A7F2E4;
  min-width: 95%;
}
.tip button:hover {
  background-color: #A7F2E4;
  color: #00494d;
}
.tip h3 {
  color: white;
}
.tip .tip-row {
  margin-bottom: 1rem;
  display: flex;
  justify-content: space-between;
  align-items: center;
}
.tip #tip-amount,
.tip #tip-total {
  margin: 0;
  font-weight: 900;
  font-size: 3rem;
  color: #26c0ab;
}